let models={
    pageInfo:{
        page: 0,
        list: ['login', 'lobby', 'game']
    },
};
{
    const CONFIG={url : window.location.href};
    let [,name,port] = CONFIG.url.split(':');
    CONFIG.assetURL = CONFIG.url+"assets/";
    CONFIG.wsURL = ["ws",name,port].join(':');
    models.CONFIG= CONFIG;
}
export default models;